import React from 'react';

import ServiceData from './ServiceData';
import Service from './Service'
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.js';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import {Grid, Row, Col, Image} from 'react-bootstrap';

export default function ServiceList() {
		const serviceElements = ServiceData.map(Services =>

			<div className="service_card" key = {Services.id}>


				<div className="d-flex service_card_image " >


						<div className="background_box">

<Link to={Services.url}>
    						<Image src={Services.img} rounded />


									<p><span>{Services.title}</span></p>
</Link>

						</div>




				</div>

			</div>

		)
		return (
			<div className="d-flex flex-wrap justify-content-center"> {serviceElements}</div>

		)
}
